@use "../variables";
@use "../mixins";
@use "./like.scss";

.photo {
  display: flex;
  flex-direction: column;
  padding-top: 24px;
  padding-right: 20px;
  padding-left: 20px;
}

.photo__description {
  padding-top: 15px;
  padding-bottom: 25px;
}

.photo__title {
  margin: 0;

  @include mixins.normal-font(variables.$black, 700);
}

.photo__author {
  margin: 0;
  margin-bottom: 11px;

  @include mixins.normal-font($case: none);
}

.photo__image-link {
  display: block;
  order: -1;
}

.photo__image {
  display: block;
  width: 100%;
  object-fit: cover;
}

@media (min-width: variables.$tablet-width) {
  .photo {
    padding: 0;
  }

  .photo__description {
    display: grid;
    grid-template-columns: 1fr max-content;
    grid-template-rows: repeat(2, min-content);
    column-gap: 20px;
    padding-inline: 20px;
    padding-block: 22px 15px;
    background-color: variables.$grey-color;
  }

  .photo__author {
    margin: 0;
  }

  .photo__like {
    grid-column: 2 / -1;
    grid-row: 1 / -1;
    justify-self: end;
    align-self: center;
  }
}

@media (min-width: variables.$desktop-width) {
  .photo {
    position: relative;
  }

  .photo__description {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(variables.$black, 0.3);
  }

  .photo__title {
    color: variables.$white;
  }

  .photo__author {
    color: variables.$white;
  }
}
