@use "../variables";
@use "../mixins";

.main-navigation {
  background-color: variables.$main-color;
}

.main-navigation--closed {
  display: none;
}

.main-navigation__list {
  padding: 0;
  margin: 0;
  list-style: none;
  background-image: linear-gradient(to top, rgba(variables.$black, 0.2) 0, rgba(variables.$black, 0) 7px);
}

.main-navigation__item {
  text-align: center;
  border-bottom: 1px solid #678fa8;
  box-shadow: inset 0 1px 0 0 rgba(variables.$white, 0.2);

  &:last-child {
    border-bottom: none;
  }
}

.main-navigation__link {
  @include mixins.medium-font(variables.$white);
  display: block;
  padding: 12px;
  text-decoration: none;
  cursor: pointer;

  &:hover,
  &:focus-visible {
    outline: none;
    color: rgba(variables.$white, 0.6);
  }

  &:active {
    color: rgba(variables.$white, 0.3);
  }
}

@media (min-width: variables.$tablet-width) {
  .main-navigation {
    display: block;
    background-color: transparent;
  }

  .main-navigation--closed {
    display: block;
  }

  .main-navigation__list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-auto-flow: dense;
    background-image: none;

    &::before {
      content: "";
      display: block;
      grid-column: 3;
      grid-row: span 2;
    }
  }

  .main-navigation__item {
    box-shadow: none;
    border: none;
  }

  .main-navigation__link {
    padding-top: 18px;
    padding-bottom: 17px;
    color: variables.$black;
    font-size: 14px;
    line-height: 21px;

    &:hover,
    &:focus-visible {
      color: variables.$hover-text-color;
      outline: none;
    }

    &:active {
      color: variables.$active-text-color;
    }
  }

  .main-navigation__link--current,
  .main-navigation__link--current:hover,
  .main-navigation__link--current:active {
    color: variables.$current-text-color;
  }
}
